export const markdownSubstitutionsMixin = {
  methods: {
    substituteContent(content, person, event) {
      content = content.replace(/->FIRST_NAME<-/g, person.firstName);
      content = content.replace(/->LAST_NAME<-/g, person.lastName);
      content = content.replace(/->NAME<-/g, person.firstName + " " + person.lastName);
      content = content.replace(/->TICKET_PRICE<-/g, `$${ event.ticketPriceCents / 100 }`);
      content = content.replace(/->PUBLIC_TICKETS_LEFT<-/g, event.standardTicketsLeft);
      content = content.replace(/->PARTNER_TICKETS_LEFT<-/g, event.partnerTicketsLeft);
      content = content.replace(/->EVENT_TITLE<-/g, event.title);
      return content;
    }
  }
};
